body.lights-off {
  filter: invert(1);
}

.logo {
  background-image: url("../assets/images/mas-logo.svg");
}

.info > div {
  padding: 4px;
  background: linear-gradient(rgba(0, 0, 0, 0.9019607843), rgba(0, 0, 0, 0.3019607843));
  box-shadow: 2px 2px 5px;
}

.info > div > div {
  background-color: #fff;
}

@media (hover: hover) or (min-width: 1024px) {
  .page {
    background-position: top right;
  }
}